<template>
  <rl-carousel v-model="slide" vertical>
    <div
      slot-scope="{ wrapperStyles, slides: { count, active } }"
      class="flex"
      style="height: 300px"
    >
      <div v-if="count > 1" class="text-center flex flex-col justify-center">
        <span
          v-for="(item, index) in count"
          :key="index"
          class="mx-1 cursor-pointer block"
          @click="
            slide = index
            autoscroll = false
          "
        >
          <svg xmlns="http://www.w3.org/2000/svg" width="10" height="10">
            <circle
              cx="5"
              cy="5"
              r="5"
              class="fill-current"
              :class="{
                'text-grey': active !== index,
                'text-grey-dark': active === index
              }"
            ></circle>
          </svg>
        </span>
      </div>
      <div class="w-full overflow-hidden">
        <div v-bind="wrapperStyles" class="flex flex-col w-full">
          <slot></slot>
        </div>
      </div>
    </div>
  </rl-carousel>
</template>

<script>
import { RlCarousel } from 'vue-renderless-carousel'

export default {
  name: 'vertical-carousel',
  components: {
    RlCarousel
  },
  data() {
    return {
      slide: 0,
      autoscroll: 2000
    }
  }
}
</script>

<style lang="less" scoped>
svg {
  transition: color 0.5s ease;
}
</style>
